.router-view {
  transition: opacity 0.2s ease;
}
.router-view.v-enter,
.router-view.v-leave {
  opacity: 0;
}
.fade-transition {
  transition: all 0.2s ease;
  opacity: 1;
}
.fade-enter,
.fade-leave {
  opacity: 0;
  transition: all 0.2s ease;
}
