.container {
  *zoom: 1;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}
.container:before {
  content: '';
  display: table;
}
.container:after {
  content: '';
  display: table;
  clear: both;
}
.slick-slider {
  margin-bottom: 0;
}
form {
  width: 100%;
}
form input,
form select,
form textarea {
  border: none !important;
  width: 100%;
  box-sizing: border-box !important;
  background: #666;
  color: #fff !important;
  font-family: 'Calibri', 'Candara', 'Segoe', 'Segoe UI', 'Optima', 'Arial', 'sans-serif' !important;
}
form input.form-error,
form select.form-error,
form textarea.form-error {
  background: #ef9090 !important;
}
form input[type=checkbox] {
  box-sizing: content-box !important;
  transition: background 0.2s;
}
form .form-row {
  width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
}
form .form-row:nth-child(n) {
  float: left;
  margin-right: 30px;
  clear: none;
}
form .form-row:last-child {
  margin-right: 0;
}
form .form-row:nth-child(1n) {
  margin-right: 0;
}
form .form-row:nth-child(1n + 1) {
  clear: left;
}
